@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
* {
    font-family: 'Pangolin', cursive;
    margin: 0;
    padding: 0;
    text-align: center;
}
*:focus {
    outline: none;
}

html, body {
    height: 100%;
}

.app {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.main {
    flex: 1 0 auto;
    width: 100vw;
    height: calc(100vh - 60px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #EEEEEE;
}

.menu {
    width: 100vw;
    height: 60px;
    background: #222831;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex: 0 0 auto;
}

.sound_button {
    padding: 0;
    border: 0;
    background-color: #222831;
    font-size: 15px;
    color: white;
    margin-left: 30px;
    cursor: pointer;
}
.sound_icon {
    margin-right: 10px;
}
.sound_on {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.sound_off {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: gray;
}

.lang_button {
    padding: 0;
    border: 0;
    background-color: #222831;
    font-size: 15px;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 30px;
    cursor: pointer;
}
.lang_icon {
    margin-left: 10px;
}

.pagination {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 30px;
}
.pagination_item {
    background: 0;
    height: 30px;
    width: 30px;
    border-radius: 100%;
    margin-right: 4px;
    margin-left: 4px;
    cursor: pointer;
    border: 1px solid white;
    display: inline-block;
}
.pagination_item:hover::after {
    content: attr(data-title);
    background: rgba(17, 17, 17, 0.9);
    border-radius: 4px;
    color: #fff;
    font-size: 16px;
    padding: 10px;
    white-space: nowrap;
    margin-bottom: 22px;
    bottom: 40px;
    position: absolute;
    z-index: 10;
    transform: translate(-50%, 10px);
    transform-origin: top;
}
.pagination_item_active {
    background-color: white;
}
.pagination_item:hover {
    background-color: white;
}
.page {
    margin: 30px;
}

.title {
    margin-bottom: 50px;
    font-size: 32px;
    color: #393E46;
}

.page h2 {
    margin-bottom: 10px;
    font-size: 26px;
    color: #393E46;
}

.page p {
    font-size: 18px;
    margin-top: 10px;
    margin-bottom: 10px;
    color: #000000;
}

.page img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 130px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 8px;
    border: 2px solid #393E46;
}

.button_next {
    background-color: #EEEEEE;
    border: 2px solid #393E46;
    border-radius: 30px;
    box-shadow: #393E46 4px 4px 0 0;
    color: #393E46;
    cursor: pointer;
    font-weight: 600;
    font-size: 20px;
    padding: 0 18px;
    line-height: 50px;
    text-align: center;
    text-decoration: none;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.button_next:hover {
    background-color: #00ADB5;
    color:white;
}

.button_next:active {
    box-shadow: #422800 2px 2px 0 0;
    transform: translate(2px, 2px);
}

.tripleBlock {
    display: flex;
    flex-direction: column;
    margin-right: 8px;
    border: 2px solid #222831;
}

.tripleBlock_1{
    height: 35px;
    width: 105px;
    background-color: rgb(251, 130, 109);
}

.tripleBlock_2{
    height: 35px;
    width: 105px;
    background-color: rgb(109, 251, 152);
}

.tripleBlock_3{
    height: 35px;
    width: 105px;
    background-color: rgb(109, 170, 251);
}

@media screen and (min-width: 640px) {
    .page {
        margin: 100px;
    }
    .title {
        font-size: 50px;
    }
    .page h2 {
        font-size: 30px;
    }
    .page p {
        font-size: 20px;
    }
    .button_next {
        font-size: 22px;
    }
    .page img {
        height: 200px;
    }
    .tripleBlock {
        margin-right: 20px;
    }
}

@media screen and (min-width: 768px) {
    .button_next {
        min-width: 120px;
        padding: 0 25px;
    }
}

@media screen and (min-width: 1024px) {
    .page {
        margin: 200px;
    }
}

@media screen and (min-width: 1280px) {

}

@media screen and (min-width: 1536px) {
    .page {
        margin: 400px;
    }
}
